import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router";

// 
import TopBar from "../components/TopBar";
import NavBar from "../components/NavBar";
import DogStaList from "../components/MyPage/DogStaList";
import DogCard from "../components/DogCard";
import UserCard from "../components/UserCard";
import WalkList from "../components/MyPage/WalkList";

// 
import { actionCreators as userActions } from "../redux/modules/user";
import { actionCreators as signActions } from "../redux/modules/sign";

//   + 
import { FiLogOut } from "react-icons/fi";
import redHeart from "../image/redHeart.png";
import grayHeart from "../image/grayHeart.png";
import dog from "../image/dog.png";
import myPage from "../image/myPage.png";
import chat from "../image/chat.png";
import edit from "../image/edit.png";

const MyPage = (props) => {
  const dispatch = useDispatch();
  const history = useHistory();

  const [status, setStatus] = useState("sta"); // ,  ,    1

  const userInfo = useSelector((state) => state.user.list); //    
  const userId = localStorage.getItem("userId"); //    ID
  const currentPageUserId = props.match.params.userId; //     ID

  // 
  const logout = () => {
    if (window.confirm(" ?")) {
      dispatch(signActions.logOut());
      history.replace("/");
    } else {
      console.log(" ");
    }
  };

  useEffect(() => {
    dispatch(userActions.getMypageMD(currentPageUserId)); //    ID  
    setStatus("sta");
  }, []);

  return (
    <Wrap>
      {/*   +   +   */}
      <TopBar>{userInfo.userNickname} </TopBar>

      {/*     */}
      <UserInfo>
        {/*   +    */}
        <UserInfoLeft>
          <UserImg src={userInfo.userImage} />

          {/*   userId    userId       . */}
          {currentPageUserId === userId && (
            <Edit
              onClick={() => {
                history.push("/userProfile");
              }}
            >
              <img src={edit} />
            </Edit>
          )}
        </UserInfoLeft>

        {/*   +  +  */}
        <UserRight>
          <div>
            <span style={{ fontWeight: "400" }}>{userInfo.userNickname}</span>
            <span style={{ color: "#5F5F5F" }}>{userInfo.userLocation}</span>
          </div>

          {currentPageUserId === userId && (
            <LogOut onClick={logout}>
              <FiLogOut size="16" />
              <span></span>
            </LogOut>
          )}
        </UserRight>
      </UserInfo>

      {/* / */}
      <Review>
        <ReviewLeft>
          <div>
            <img src={redHeart} />
            <img src={redHeart} />
            <img src={redHeart} />
            <img src={redHeart} />
            <img src={grayHeart} />
          </div>
          <p>4.5/5</p>
        </ReviewLeft>
        <ReviewRight></ReviewRight>
      </Review>

      {/*     */}
      <Buttons>
        {/* DogStaList  -       */}
        <div
          onClick={() => {
            setStatus("sta");
          }}
        >
          <img src={dog} style={{ width: "24px", height: "21px" }} />
          <span></span>
        </div>

        {/* InfoList  -     */}
        <div
          onClick={() => {
            setStatus("dog");
          }}
        >
          <img src={chat} style={{ width: "20px", height: "16px" }} />
          <span> </span>
        </div>
        {/* WalkList  -      */}
        <div
          onClick={() => {
            setStatus("list");
          }}
        >
          <img src={myPage} style={{ width: "16px", height: "20px" }} />
          <span> </span>
        </div>
      </Buttons>

      {/* ,  ,   */}
      <Cards>
        {/* DogStaList -       */}
        {status === "sta" && <DogStaList userId={currentPageUserId} />}

        {/* InfoList -     */}
        {status === "dog" && (
          <div>
            <UserCard post={userInfo} userId={currentPageUserId} />
            <DogCard post={userInfo} userId={currentPageUserId} />
          </div>
        )}

        {/* WalkList      */}
        {status === "list" && (
          <WalkList post={userInfo} userId={currentPageUserId} />
        )}
      </Cards>

      {/*   */}
      <NavBar />
    </Wrap>
  );
};

const Wrap = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 20px;
`;
const UserInfo = styled.div`
  position: relative;

  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;

  width: 100%;
  height: 88px;

  margin-bottom: 24px;
`;
const UserInfoLeft = styled.div`
  position: relative;

  width: 91px;
  height: 88px;

  margin-right: 16px;
`;
const UserRight = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
`;
const UserImg = styled.img`
  width: 83px;
  height: 83px;
  padding: 2px;

  margin-right: 14.5px;
  border-radius: 50%;
  object-fit: cover;
`;
const Edit = styled.div`
  position: absolute;
  bottom: 0;
  right: 0;
  cursor: pointer;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  width: 36px;
  height: 36px;
  padding: 6px;
  border: 2px solid black;
  border-radius: 50%;
  background-color: #fff;

  img {
    width: 22px;
    height: 22px;
  }
`;
const Buttons = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  height: 48px;
  padding: 0 24px;

  div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    cursor: pointer;
  }
  img {
    width: 24px;
    height: 24px;
    margin-bottom: 8px;
  }

  span {
    font-size: 14px;
  }
`;
const LogOut = styled.button`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  border: none;
  background-color: transparent;
  cursor: pointer;

  color: #5f5f5f;
`;
const Review = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  height: 66px;
  width: 100%;
  margin-bottom: 22px;
  border-top: 1px solid #c4c4c4;
  border-bottom: 1px solid #c4c4c4;
`;
const ReviewLeft = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;

  div {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    color: #5f5f5f;
  }

  img {
    width: 20px;
    height: 18px;
    margin-right: 2px;
  }

  p {
    color: #5f5f5f;
  }
`;
const ReviewRight = styled.div`
  color: #5f5f5f;
`;
const Cards = styled.div`
  width: 100%;
  margin: 24px 0 200px 0;
  padding-top: 24px;
  border-top: 1px solid #c4c4c4;
`;

export default MyPage;
