import React, { useState } from "react";
import styled from "styled-components";
import { CSSTransition } from "react-transition-group";
import { MdLocationOn } from "react-icons/md";

import "../../../nav.css";
import { FaBars, FaChevronLeft, FaChevronRight } from "react-icons/fa"; // ,<,>
import "./LocationCategory.css";
const LocationCategory = () => {
  return (
    <div>
      <Navbar>
        <NavItem icon={<FaBars />}>
          <DropdownMenuThree />
        </NavItem>
      </Navbar>
    </div>
  );
};

function Navbar(props) {
  return (
    <nav className="navbar">
      <ul className="navbar-nav">{props.children}</ul>
    </nav>
  );
}

function NavItem(props) {
  const [open, setOpen] = useState(false);
  return (
    <div className="nav-item">
      <a className="icon-button" onClick={() => setOpen(!open)}>
        {props.icon}
      </a>
      {open && props.children}
    </div>
  );
}

function DropdownMenuThree() {
  const [activeMenu, setActiveMenu] = useState("main");
  const [open, setOpen] = useState(false);
  function DropdownItem(props) {
    return (
      <a
        href="#"
        className="menu-item"
        onClick={() => props.goToMenu && setActiveMenu(props.goToMenu)}
      >
        {props.children}
      </a>
    );
  }

  return (
    <div className="dropdown">
      <CSSTransition
        in={activeMenu === "main"}
        unmountOnExit
        timeout={500}
        classNames="menu-primary"
      >
        <div className="menu">
          <DropdownItem goToMenu="top"></DropdownItem>
          <DropdownItem goToMenu="bottom"></DropdownItem>
          <DropdownItem goToMenu="accessory"></DropdownItem>
        </div>
      </CSSTransition>

      <CSSTransition
        in={activeMenu === "top"}
        unmountOnExit
        timeout={500}
        classNames="menu-secondary"
      >
        <div className="menu">
          <DropdownItem goToMenu="main">
            <FaChevronLeft />
          </DropdownItem>
          <DropdownItem></DropdownItem>
          <DropdownItem></DropdownItem>
          <DropdownItem></DropdownItem>
          <DropdownItem></DropdownItem>
          <DropdownItem></DropdownItem>
        </div>
      </CSSTransition>

      <CSSTransition
        in={activeMenu === "bottom"}
        unmountOnExit
        timeout={500}
        classNames="menu-secondary"
      >
        <div className="menu">
          <DropdownItem goToMenu="main">
            <FaChevronLeft />
          </DropdownItem>
          <DropdownItem></DropdownItem>
          <DropdownItem></DropdownItem>
        </div>
      </CSSTransition>

      <CSSTransition
        in={activeMenu === "accessory"}
        unmountOnExit
        timeout={500}
        classNames="menu-secondary"
      >
        <div className="menu">
          <DropdownItem goToMenu="main">
            <FaChevronLeft />
          </DropdownItem>
          <DropdownItem></DropdownItem>
        </div>
      </CSSTransition>
    </div>
  );
}

export default LocationCategory;
